export async function genExt() {
  try {
    await Excel.run(async (context)=> {
      fqdn = document.getElementById("excel-fqdn").value;
      const extPage = context.workbook.worksheets.getItem("Interni");
      const extPageRange = await advSelect(context, {columns: {start: "a", end: "j"}});
      const extPageData = extPage.getRange(extPageRange);
      extPageData.load("values");
      await context.sync();
      var extDataOut = new Array(extPageData.values.length);
      for (let i = 0; i < extPageData.values.length; i++) {
        let template = git_values.template.ext.slice();
        template = combineArray(extPageData.values[i], template, git_values.template.extOffset,git_values.template.extOffset.length);
        for (let i = 0; i < git_values.template.pinOffset.length; i++) {
          template[git_values.template.pinOffset[i]] = genPass(0, git_values.credentials.pin.length);
        }
        for (let i = 0; i < git_values.template.passOffset.length; i++) {
          template[git_values.template.passOffset[i]] = genPass(1, git_values.credentials.password.length,git_values.credentials.password.pattern);
        }
        if(extPageData.values[i][6] != ""){
          let phone = getPhone(extPageData.values[i]);
          template = combineArray(phone, template, git_values.template.phoneOffset);
        }
        console.log(template);
        extDataOut[i]=template;
      }
      console.log(extDataOut);
      const outExtPage = context.workbook.worksheets.getItem("Uscita interni");
      const outExtPageRange = outExtPage.getRange(`A2:BH${extDataOut.length+1}`);
      outExtPageRange.values = extDataOut;

    });
  } catch (error) {
    console.log(error);
  }
  
}

