export async function genGnr(){
  try {
    await Excel.run(async (context) => {
      //PAGES 
      const extPage = context.workbook.worksheets.getItem("Interni");
      const gnrPage = context.workbook.worksheets.getItem("GNR");
      // GEN NUMBERS
      const number = document.getElementById("excel-gnr-number").value;
      const day = document.getElementById("excel-gnr-day").value;
      const night = document.getElementById("excel-gnr-night").value;
      const trunk = document.getElementById("excel-trunk").value;
      let trunkNumber = [];
      let gnr = number.split("-");
      let gnrLenght = gnr[1].length;
      const gnrNumber = gnr[0].slice(0, gnr[0].length-gnrLenght);
      gnr[0] = Number(gnr[0].slice(-gnrLenght));
      gnr[1] = Number(gnr[1]);
      
      for (let i = gnr[0]; i <= gnr[1]- gnr[0]; i++) {
        let tmpNumber = gnrNumber;
        if(gnrLenght == 2 && i < 10){
          tmpNumber += "0"
        }
        if(gnrLenght == 3){
          if(i < 10){
            tmpNumber += "00";
          }
          if(i >= 10 && i < 100){
            tmpNumber += "0";  
          }
        }
        tmpNumber += i;
        trunkNumber.push([i-gnr[0]+2,"GNR",1,tmpNumber,trunk,2,day,0,0,,,2,night,0]);
      }
      const extPageRange = await advSelect(context, {columns: {start: "a", end: "a"}});
      const extPageData = extPage.getRange(extPageRange);
      let exts = extPageData.load("values");
      await context.sync();
      for (let i = 0; i < exts.values.length; i++) {
        for (let j = 0; j < trunkNumber.length; j++) {
          if(trunkNumber[j][3].slice(-exts.values[i][0].length) == exts.values[i][0]){
            trunkNumber[j][1] = "DIRETTO";
            trunkNumber[j][6] = exts.values[i][0];
            trunkNumber[j][12] = exts.values[i][0];
          }
        }
      }

      const gnrPageRange = gnrPage.getRange(`A2:N${trunkNumber.length+1}`);
      gnrPageRange.values = trunkNumber;

    });
  } catch (error) {
    console.log(error);
  }
}

export async function addPhone(){
  try {
    await Excel.run(async (context) => {
      const extPage = context.workbook.worksheets.getItem("Interni");
      const phone = document.getElementById("excel-phone").value;
      const phoneRange = await advSelect(context, {columns: {start: "g", end: "g"}});
      const sbcRange = await advSelect(context, {columns: {start: "i", end: "i"}});
      const phoneRangeData = extPage.getRange(phoneRange);
      const sbcRangeData = extPage.getRange(sbcRange);
      phoneRangeData.values = "Sel. Modello";
      sbcRangeData.values = "Sel. SBC";
      phoneRangeData.dataValidation.rule = {
        list: {
          inCellDropDown: true,
          source: `${git_values.phones[phone].models.join(",")}`
        }
      }
      let tmpSbc = [];
      for (let i = 0; i < git_values.template.sbc.length; i++) {
        const sbc = git_values.template.sbc[i].name;
        tmpSbc.push(sbc)
      }
      sbcRangeData.dataValidation.rule = {
        list: {
          inCellDropDown: true,
          source: `${tmpSbc.join(",")}`
        }
      }
    })
  }catch(error){
    console.log(error)
  }
}

export async function genPages() {
  try {
    await Excel.run(async (context) => {
      //CREATE PAGE 
      const names = context.workbook.worksheets.load("items/name");
      let exitsPages = new Array();
      await context.sync();
      names.items.forEach((pageName) => {
        exitsPages.push(pageName.name);
      })
      git_values.pages.forEach((page, i) => {
        if (exitsPages.includes(page.name) == false) {
          context.workbook.worksheets.add(page.name);
        }
      })
    });

    //LOAD DATA
    await Excel.run(async (context) => {
      git_values.pages.forEach(page => {
          const columns = numberToColumn(page.cells.length-1);
          const selPage = context.workbook.worksheets.getItem(page.name);
          const range = selPage.getRange(`A1:${columns}1`);
          range.load("values");
          range.values = [page.cells];
          const range2 = selPage.getRange(`A:${columns}`);
          range2.numberFormat = "@";
      });
    });

  }catch (error) {
    console.log(error);
  }
}

// ADV SELECT
async function advSelect(context, settings){
  //LOAD SELECTED RANGE
  let tmpRange = context.workbook.getSelectedRange();
  tmpRange.load("address");
  await context.sync();
  let range = ((tmpRange.address).split("!"))[1];

  // AVOID 1ST ROW SELECTION
  let rows = range.split(":");
  for(let i = 0; i < rows.length; i++){
    rows[i] = rows[i].replace(regexRows, "");
    if(rows[i] == 1){
      rows[i] = 2;
    }
  }
 
  //COMBINE SELECTED ROWS WT PASSED COLUMN
  if(rows.length == 1 && settings.columns.start == settings.columns.end){
    return `${settings.columns.start}${rows[0]}`;
  }
  if(rows.length == 1 && settings.columns.start != settings.columns.end){
    return `${settings.columns.start}${rows[0]}:${settings.columns.end}${rows[0]}`;;
  }
  return `${settings.columns.start}${rows[0]}:${settings.columns.end}${rows[1]}`;;
}

